import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FormsModule } from '@angular/forms';

import { UserService } from 'src/app/api/controllers/User';
import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { CookieService } from 'ngx-cookie-service';
import { SharedToasterService } from '../../../../shared/services/shared-toaster.service';

import * as moment from 'moment';

@Component({
  selector: 'app-checkin-visitor',
  templateUrl: './checkin-visitor.component.html',
  styleUrls: ['./checkin-visitor.component.scss']
})
export class CheckinVisitorComponent implements OnInit {

  isEditVisitor:boolean = false;

  isVisitorSubmitted:boolean = false;
  isVisitorAdded:boolean = false;

  isError:boolean = false;
  errorMessage:string = "";

  visitor:any;

  visitByData:any;
  visitTypeId:string = "";

  apartmentUnitId:number;

  isExpectedVisitor:boolean = false;

  visitorStatus:string = "";
  minDate;
  isUnitVisitor = false;
  iscommunityVisitor = false;
  visitCategoryData;
  visitCategoryId;
  blockUnitData: any;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private apartmentService:ApartmentService,
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private SharedToaster:SharedToasterService,
  ) {

    this.minDate = moment(new Date()).format('YYYY-MM-DD');
   }


  isCheckout(){
    return this.visitorStatus == 'checkout' ? true : false;
  }

  submitAddVisitorForm(form) {

  	this.isVisitorSubmitted = true;
    this.isError = false;

        let visitorDetails = {
      
          "apartmentUnitId": this.apartmentUnitId,
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "visitorName": this.visitor.expectedVisitorName,
          "visitorCount": this.visitor.expectedVisitorCount,
          "visitorPhone": this.visitor.expectedVisitorPhone,
          "visitorVehicleTypeId": 1,
          "visitorVehicleNumber": "string",
          "visitingPlaceId": 1,
          "passNumber": this.visitor.passNumber,
          "isCheckedIn": true,
          "visitorInTime": this.visitor.expectedVisitorInTime,
          "isCheckedOut": false,
          "visitorOutTime": null,
          "expectedDuration": this.visitor.expectedDurationInHours,
          "meetingPersonId": 1,
          "meetingPersonName": "string",
          "visitTypeId": parseInt(this.visitTypeId),
          "visitCategoryId": parseInt(this.visitCategoryId),
          "visitorsOrg": "string",
          "entryGateId": null,
          "exitGateId": null,
          "purpose": this.visitor.purpose,
          "enteredBy": parseInt(this.cookieService.get('userId')),
          "isActive": true,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": "2019-11-18T17:08:51.188Z",
          "updatedBy": 0,
          "updatedOn": "2019-11-18T17:08:51.188Z"

      }

      let params = {
        visitor: visitorDetails
      }

        this.visitorService.addVisitor(params).subscribe((res:any) => {
        
          if(res.message) {
            this.isVisitorSubmitted = false;
          }
          else {

            this.isVisitorSubmitted = false;
            this.isError = true;
            this.errorMessage = res.errorMessage;
          }

        },
        error => {
          
          this.isVisitorSubmitted = false;
          this.isError = true;
          this.errorMessage = error;

        });

      }
  
  ngOnInit() {
    
    this.visitor = {};
    
    let visitParams = {
      LookupTypeId: 15
    }

       //visit type
    this.lookupService.getLookupValueByLookupTypeId(visitParams).subscribe((res:any) => {
          
      this.visitByData = res;

    },
    error => {
      
    }
    );

    let visitCategoryParams = {
      LookupTypeId: 100
    }

    //visit type
    this.lookupService.getLookupValueByLookupTypeId(visitCategoryParams).subscribe((res:any) => {
          
      this.visitCategoryData = res;

    },
    error => {
      
    }
    );

    let cateogoryParams = {
      userId: parseInt(this.cookieService.get('userId'))
    }

	this.apartmentService.getApartmentBlockUnitByUserId(cateogoryParams).subscribe((res:any) => {

		this.apartmentUnitId = res[0].apartmentBlockUnitId;

	},
	error => {

	}
  );
  this.visitor.expectedVisitorInTime = moment(new Date());
  
  this.apartmentService.getAllApartmentBlockUnits().subscribe((res:any) => {

    this.blockUnitData = res;
    
  });
  }

  changeVisitorType(id){
    console.log(id);
    let result = this.visitByData.filter(filterId => id == filterId.lookupValueId);
    console.log(result);
    this.isUnitVisitor = (result[0].lookupValueName == 'Unit Visitor') ? true : false;
    this.iscommunityVisitor  = (result[0].lookupValueName == "Community Visitor") ? true : false;
  }

  expectedDurationChange(){
    if(this.minDate > moment(this.visitor.expectedVisitorInTime).format('YYYY-MM-DD')){
      this.visitor.expectedVisitorInTime = undefined;
      this.SharedToaster.openSnackBar("Please Select valid Date",'');
      return;
    }
    
    if(this.minDate > moment(this.visitor.expectedVisitorOutTime).format('YYYY-MM-DD')){
      this.visitor.expectedVisitorOutTime = undefined;
      this.SharedToaster.openSnackBar("Please Select Out-time greater than In-time",'');
      return;
    }
    console.log(this.visitor.expectedVisitorOutTime);
    if(this.visitor.expectedVisitorOutTime){
      if(moment(this.visitor.expectedVisitorInTime).format('YYYY-MM-DD') > moment(this.visitor.expectedVisitorOutTime).format('YYYY-MM-DD')){
        this.visitor.expectedVisitorOutTime = undefined;
        this.SharedToaster.openSnackBar("Please Select Out-time greater than In-time",'');
        return;
      }
    }
    
    let inDate = moment(this.visitor.expectedVisitorInTime);
    let outDate = moment(this.visitor.expectedVisitorOutTime);
    let diffDuration = moment.duration(outDate.diff(inDate));
    this.visitor.expectedDuration = diffDuration.days()+" days "+this.isSingleDigit(diffDuration.hours())+":"+ this.isSingleDigit(diffDuration.minutes())+" hours";

    let sendDuration = moment.duration(outDate.diff(inDate));
    let hours = sendDuration.asHours();
    this.visitor.expectedDurationInHours = Math.round(hours);
  }
  isSingleDigit(digit){
    if(digit <= 0){
      return '00';
    }else{
      return (digit.toString().length > 1) ? digit : '0'+digit;
    }
    
  }

}
